 body {
    margin: 0;
    max-width: 100vw;
    height: auto;
    background: url("/images/Kites.jpg") fixed;
    background-size: cover;
}

.darktext {
    color: #12343b
}


.lighttext {
    color: #c89666;
}

.browntext {
    color: #12343b
}

.brownborder {
    border: solid;
    border-color: #c89666;
}

.darkbackground {
    background-color: #12343b;
}

.lightbackground {
    background-color: #e1b382;
}


.homecard {
    background-color: #e1b382;
    width: 80vw;
    text-align: center;
    height: auto;
}

.homeimg {
    width: 80vw;
    height: 40vw;
}

.hometitle {
    font-size: large;
    color: #12343b;
    justify-content: center;
    align-items: center;
}

.homedesc {
    font-size: medium;
    padding-left: 3%;
    padding-right: 3%;
    padding-bottom: 5%;
    vertical-align: center;
    color: #12343b;
}

.educationchip {
    font-size: 10.5px;
    background-color: Snow;
    border: solid;
    border-color: #F76902;
    height: auto;
    border-radius: 20px;
    /* max-width: 80vw; */

}

.educationcard {
    width: 80vw;
}
.skillscard {
    width: 90vw;
}

.imagegrid {
    width: 80vw;
    background-color: #e1b382;
    padding-bottom: 5px;
}

.ethanlucas {
    width:80vw;
    background-color: #e1b382;
    padding-bottom: 5px;
}

.buttonheader:active {
    background: #12343b;
    /* -webkit-box-shadow: inset 0px 0px 5px #c1c1c1; */
       /* -moz-box-shadow: inset 0px 0px 5px #c1c1c1; */
            /* box-shadow: inset 0px 0px 5px #c1c1c1; */
     outline: none;
}

.homebuttonheader {
    background-color: Transparent;
    border: none;
    width: 100vw;
    padding-top: 10px;
}

.experiencegrid {
    padding-top:40px;
}
.skillsgrid {
    padding-top: 20px;
}

@media only screen and (min-width: 768px) {
    .homecard {
        background-color: #e1b382;
        width: 45vw;
        text-align: center;
    }
    .homeimg {
        width: 45vw;
        height: auto;
    }
    .educationchip {
        font-size: large;
        background-color: Snow;
        border: solid;
        border-color: #F76902;
        height: auto;
        border-radius: 20px;
    }
    .educationcard {
        width: 40vw;
    }
    .skillscard {
        width: 80vw;
    }
    .imagegrid {
        width: 50vw;
        background-color: #e1b382;
    }
    
    .ethanlucas {
        width:25vw;
        background-color: #e1b382;
    }
    .homebuttonheader {
        background-color: Transparent;
        border: none;
        width: 30vw;
        padding-top: 10px;
    }
    .headericon {
        padding-top: 25px;
    }
    .headerspacer {
        width: 20vw;
    }
    .experiencegrid {
        padding-top:40px;
    }
    .skillsgrid {
        padding-top: 40px;
    }
}

@media only screen and (min-width: 1000px) {
    .homegrid {
        padding-top: 50px;
    }
    .homecard {
        background-color: #e1b382;
        width: 30vw;
        text-align: center;
    }
    .homeimg {
        width: 30vw;
        height: auto;
        max-height: 20vw;

    }
    .educationchip {
        font-size: x-large;
        background-color: Snow;
        border: solid;
        border-color: #F76902;
        height: auto;
        border-radius: 20px;
    }
    .educationcard {
        width: 30vw;
    }
    .skillscard {
        width: 31vw;
    }
    .homebuttonheader {
        background-color: Transparent;
        border: none;
        width: 25vw;
        padding-top: 10px;
    }
    .headerspacer {
        width: 40vw;
    }
    .experiencegrid {
        padding-top:40px;
    }
    .skillsgrid {
        padding-top: 100px;
    }
}